.header-color{
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid{
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position:fixed;
top:0;
left:0;
width: 100%;
height: 100%;

}
/* Resume Page css*/
.resume-right-col{
  background:#27221f;
  color: white;
  padding:2em;
}
.contact-body{
  margin:auto;
  background-attachment: fixed;
  background-image: url("https://media.tenor.com/images/0999faacefe3e7712166f78d7ace4951/tenor.gif");
  position:fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top:2em;

  }
  .contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 700px;
    background:white;
    color:black;
    border:2px solid red;
    margin-bottom: 20px;
  }
  .contact-grid h2{
    font-family: "Anton",sans-serif;
  }
  .contact-grid p{
    font-family: "Oxygen",sans-serif;font-weight: bold;
  }
  .contact-grid hr{
    border-top: 5px dotted black;
    width: 50%;
    margin:auto;
  }
  .contact-list i{
    font-size: 50px;
    padding-right: 5rem;
  }


